@import "variable";
@import "sprite";

.catalog {
  margin-bottom: 60px;
  .tabs-category {
    display: flex;
    justify-content: space-between;
    .category-item {
      $sprites: $sprite-catalog1, $sprite-catalog2, $sprite-catalog3, $sprite-catalog4, $sprite-catalog5;
      $sprites1: $sprite-catalogh1, $sprite-catalogh2, $sprite-catalogh3, $sprite-catalogh4, $sprite-catalogh5;
      @each $icon in $sprites {
        $i: index($sprites, $icon);

        &:nth-child(#{$i}) {
          &:before {
            content: '';
            display: block;
            @include sprite($icon);
          }
        }
      }
      @each $icon in $sprites1 {
        $u: index($sprites1, $icon);
        &:nth-child(#{$u}) {
          &.active {
            &:before {
              @include sprite($icon);
            }
          }
          &:hover {
            cursor: pointer;
            &:before {
              @include sprite($icon);
            }
          }
        }
      }
    }
  }

  .products-category {
    margin-top: 50px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #5b5b5b;
    .products-list {
      display: none;
      justify-content: flex-start;
      flex-wrap: wrap;
      &.active {
        display: flex;
      }
      .product-item {
        width: 480px;
        margin: 0 40px;
        .img-item {
          height: 340px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: 10px;
        }
        .title-products {
          background-color: #5d5d5d;
          color: white;
          font-size: 30px;
          display: flex;
          font-weight: 700;
          justify-content: space-between;
          padding: 8px 15px 8px 20px;
          margin-bottom: 10px;
          .name {
            text-transform: uppercase;
          }
          .price{
            text-align: right;
          }
        }
        .about-products {
          background-color: #f4eee7;
          padding: 25px 35px;

          .title-about {
            color: #d75920;
            font-size: 20px;
            text-transform: uppercase;
            font-weight: 700;
            margin-bottom: 20px;
          }

        }
        .row-data {
          display: flex;
          font-size: 18px;
          margin-bottom: 20px;
          .desc {
            margin-right: 5px;
          }
          &:last-child {
            margin-bottom: 0;
          }
        }
        .model-item {
          padding: 10px 30px 15px;
          .item-info {
            display: flex;
            justify-content: space-between;
            .item-price, .item-area, .item-name {
              flex: 0 1 100%;
            }
            .title-value {
              font-size: 18px;
            }
            .value {
              margin: 20px 0;
            }
            .item-price {
              margin-left: -25px;
              .value {
                font-size: 20px;
                span {
                  font-size: 14px;
                  text-decoration: line-through;
                }
              }
            }
          }
          &:nth-child(even) {
            background-color: #ede7e0;
          }
          &:nth-child(odd) {
            background-color: #e7e2db;
          }
          .item-buttons {
            display: flex;
            justify-content: space-between;
            > div {
              background-color: #d75920;
              color: white;
              text-align: center;
              padding: 8px 0;
              width: 195px;
              &:hover {
                cursor: pointer;
              }
            }
          }
          .hidden-character {
            width: 625px;
            height: 84vh;
            overflow: hidden;
            overflow-y: scroll;
            background-color: white;
            border: 3px solid #d54f12;
            position: fixed;
            left: 50%;
            top: -500px;
            margin-left: -312.5px;
            opacity: 0;
            margin-top: 0;
            visibility: hidden;
            padding: 35px 15px 35px 33px;
            z-index: 9999999999;
            @include scrollbar();
            @include transition();
            &.open {
              opacity: 1;
              visibility: visible;
              top: 10%;
            }
            .title-character {
              font-size: 30px;
              margin-bottom: 20px;
              color: #d8591e;
              span {
                text-transform: uppercase;
              }
            }
            .sub-title {
              margin: 25px 0 20px;
              font-size: 18px;
              color: #d8591e;
            }
            .row-data {
              font-size: 14px;
              margin-bottom: 0;
              line-height: 18px;
              .desc {
                width: 310px;
              }
              .value {
                width: 260px;
              }
            }
          }
        }
      }
    }
  }

  @media all and (max-width: 1139px){
    .tabs-category{
      .category-item{
        width: 20%;
        overflow: hidden;
        &:before{
          width: 100%;
        }
      }
    }
    .products-category{
      .products-list{
        justify-content: space-between;
        .product-item{
          margin: 0 5px;
        }
      }
    }
  }

  @media all and (max-width: 991px) {
    .tabs-category {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .category-item {
        width: initial;
        overflow: visible;
        margin-bottom: 5px;
      }
    }

    .products-category {
      .products-list {
        .product-item {
          width: 100%;
          max-width: 400px;
          margin: 0 auto;
          .img-item{
            img{
              width: 100%;
              height: initial;
            }
          }
          .about-products{
            padding: 25px 10px;
          }
          .row-data{
            .desc{
              width: 70%;
            }
            .value{
              width: 30%;
            }
          }
          .model-item{
            padding: 10px 10px 15px;
            .item-info{
              .item-price{
                margin-left: 0;
                .value{
                  display: flex;
                  flex-direction: column;
                }
              }
            }
            .item-buttons{
              > div{
                width: 140px;
              }
            }
            .hidden-character{
              max-width: 320px;
              left: 50%;
              margin-left: -160px;
              &.open{
                top:20%;
              }
              .title-character{
                font-size: 20px;
              }
            }
          }
        }
      }
    }
  }
}