
$inludePath: "/wp-content/themes/conditioning/inc/";

@mixin transition($duration:0.5s)  {
  -o-transition: all $duration;
  -moz-transition: all $duration;
  -webkit-transition: all $duration;
  transition: all $duration;
}

@mixin scrollbar($width:6px,$back-color:#f5f5f5,$back-second-color:#5d5d5d,$border-radius:10px){
  &::-webkit-scrollbar {
    width: $width;
    background-color: $back-color;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: $border-radius;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: $back-second-color;
  }
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: $border-radius;
    background-color: $back-color;
  }
}
::-webkit-input-placeholder {color:#717177;}
::-moz-placeholder          {color:#717177;}/* Firefox 19+ */
:-moz-placeholder           {color:#717177;}/* Firefox 18- */
:-ms-input-placeholder      {color:#717177;}

