@import "icons";
@import 'variable.scss';

.head-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 23px 40px;
  color: #5b5b5b;
  .address,.time-work{
    display: flex;
    align-items: center;
    &:before{
      font-size: 31px;
      margin-right: 15px;
    }
  }
  .address {
    @include icon(marker);

  }
  .time-work {
    @include icon(clock);
  }
  .phone-list {
    display: flex;
    flex-direction: column;
    .phone {
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #5b5b5b;
      @include icon(phone);
      &:first-child{
        margin-bottom: 5px;
      }
      &:before{
        font-size: 23px;
        margin-right: 15px;
      }
    }
  }

  .callback {
    background-color: #d75920;
    padding: 11px 40px;
    text-align: center;
    color: white;
    @include transition();
    &:hover {
      cursor: pointer;
    }
  }

  .mobile-menu {
    display: none;
    .line {
      background-color: #595959;
      position: relative;
      height: 5px;
      width: 32px;
      margin: 2px 0;
      transition: all .5s;
    }
    &:hover {
      cursor: pointer;
    }
    &.active {
      .line {
        opacity: 0;
        &:first-child {
          opacity: 1;
          transform: rotate(45deg);
          top: 9px;
          height: 5px;
        }
        &:last-child {
          opacity: 1;
          transform: rotate(-45deg);
          top: -9px;
          height: 5px;
        }
      }

    }
  }
}

.menu {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  li {
    list-style: none;
    width: 100%;
    text-align: center;
    background-color: #5d5d5d;
    @include transition();
    margin-right: 2px;
    &:last-child{
      margin-right: 0;
    }
    a {
      display: block;
      color: white;
      font-size: 14px;
      padding: 13px 0;
    }
    &:hover {
      background-color: #d75920;
    }
  }
}

@media all and (max-width: 991px) {
  .head-info {
    padding: 25px 0;
    width: 275px;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .logo {
      margin: 0 auto;
    }
    .phone-list{
      margin: 23px 0;
      .phone{
        &:before{
          font-size: 24px;
        }
      }
    }
    .address,.time-work{
      display: none;
    }
    .right-block {
      display: flex;
      width: 100%;
      justify-content: space-between;
    }
    .mobile-menu {
      display: flex;
      flex-direction: column;
      padding: 7px 0;
    }
  }
  .primary-menu {
    display: none;
    left: 0;
    top: 247px;
    position: absolute;
    width: 100%;
    z-index: 10;
    margin-left: 0;
    .menu {
      flex-direction: column;
      li {
        margin: 0;
        a {
          display: block;
          text-align: center;
          padding: 15px 0;
          color: white;
          margin-right: 0;
          border-bottom: 1px solid white;
          font-size: 14px;
        }
      }
    }
  }
}