@import 'variable.scss';

.footer{

  .contacts{
    position: absolute;
    top:0;
    left: 85px;
    width: 290px;
    height: 360px;
    display: flex;
    flex-direction: column;
    color:white;
    background-color: #5f5d5c;
    font-family: 'Roboto', sans-serif;
    padding: 25px 32px;
    img{
      display: block;
      margin: 0 auto;
    }
    .contact-title{
      font-size: 30px;
      margin: 25px 0 20px;
    }
    .address,.time-work{
      display: flex;
      align-items: center;
      &:before{
        font-size: 31px;
        margin-right: 15px;
      }
    }
    .address {
      @include icon(marker);

    }
    .time-work {
      margin: 30px 0 20px;
      @include icon(clock);
    }
    .phone-list {

      display: flex;
      flex-direction: column;
      .phone {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: white;
        @include icon(phone);
        &:first-child{
          margin-bottom: 5px;
        }
        &:before{
          font-size: 22px;
          margin-right: 15px;
        }
      }
    }
  }

  .copyring{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 22px 0;
    font-size: 13px;
    color:#6c6b6a;
    img{
      margin-bottom: 10px;
    }
  }

  @media all and (max-width: 991px){
    #map{
      display: none;
    }
    .contacts{
      position: relative;
      top:0;
      left: initial;
      width: 100%;
      max-width: 320px;
      padding: 25px 32px;
      margin: 0 auto;
    }
  }
}