@import 'variable.scss';

.modal-wrapper.open {
  opacity: 1;
  visibility: visible;
}

.modal-wrapper.open .modal-block {
  top: 10%;
}

.modal-wrapper {
  background: rgba(0, 0, 0, 0.51);
  bottom: 0;
  height: 100%;
  left: 0;
  opacity: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  width: 100%;
  z-index: 120000;
  font-family: 'Roboto', sans-serif;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#popup__form {
  position: absolute;
  left: 50%;
  margin-top: 170px;
  margin-left: -157.5px;
  width: 315px;
  height: 190px;
  text-align: center;
  color: #373842;
  z-index: 9999999;
  .modal-form {
    padding-top: 30px;
    input {
      width: 250px;
      background-color: transparent;
      border: 1px solid #bababa;
      padding: 6px 10px 6px 15px;
      font-size: 14px;
      display: block;
      margin: 0 auto 15px;
      color: #717177;
      font-style: italic;
    }
    .submit {
      width: 200px;
      margin: 0 auto;
      border: 1px solid white;
      padding: 12px 0;
      background-color: #d75920;
      color: white;
      text-align: center;
      font-size: 16px;
      @include transition();
      &:hover {
        cursor: pointer;
        transform: scale(0.95);
      }
    }
  }
}

#stock-form {
  position: absolute;
  left: 50%;
  top: 0;
  margin-left: -500px;
  z-index: 3;
  width: 1000px;
  height: 84vh;
  overflow-x: hidden;
  overflow-y: scroll;
  border: 5px solid #d54f12;
  background-color: #fff;
  padding: 40px 37px;
  @include scrollbar();
  .top-stock {
    display: flex;
    justify-content: space-between;
    color: #fff;
    .left {
      color: #666565;
      width: 468px;
      .title-stock {
        font-size: 20px;
        color: #d54f12;
      }
      ul {
        padding: 0;
        margin-top: 30px;
        li {
          list-style: none;
          &:before {
            content: '•';
            display: inline-block;
            color: #d54f12;
            margin-right: 5px;
          }
        }
      }
    }
    .right {
      width: 385px;
      .block-info {
        font-size: 20px;
        background-color: #d54f12;
        padding: 20px 40px;
        span {
          font-size: 32px;
          width: 42px;
          display: inline-block;
          margin-right: 15px;
        }
        strong {
          text-transform: uppercase;
        }
        p {
          margin: 0;
          &:nth-child(2) {
            margin: 10px 0 15px;
          }
        }
      }
    }
  }
  .bottom-stock {
    margin-top: 35px;
    display: flex;
    flex-direction: column;
    .bottom-title {
      font-size: 20px;
      color: #d54f12;

    }
    .list-card {
      margin-top: 30px;
      display: flex;
      justify-content: space-between;
      .card-item {
        width: 272px;
        color: #666565;
        .title-card {
          font-size: 24px;
          margin: 18px 0;
        }
      }
    }
  }
  @media all and (max-width: 1023px){
    width: 100%;
    margin-left: 0;
    left: 0;
    padding: 40px 10px;
    .top-stock{
      flex-direction: column;
      .left,.right{
        width: 100%;
        img{
          width: 100%;
          max-width: 350px;
          display: block;
          margin: 0 auto;
        }
      }
    }
    .bottom-stock{
      flex-direction: column;
      .list-card{
        flex-direction: column;
        align-items: center;
        .card-item{
          margin-bottom: 15px;
        }
      }
    }
  }
}

#service-form {
  position: fixed;
  left: 50%;
  top: 0;
  margin-left: -500px;
  z-index: 999999;
  width: 1000px;
  height: 84vh;
  overflow-x: hidden;
  overflow-y: scroll;
  border: 5px solid #d54f12;
  background-color: #fff;
  padding: 40px 37px;
  font-size: 16px;
  color: #666565;
  @include scrollbar();
  .information-title {
    color: #5d5d5d;
    font-size: 30px;
    text-transform: uppercase;
  }
  .information-second-title {
    color: #d54f12;
    font-size: 20px;
    margin-top: -20px;
  }
  .orange-text {
    color: #d54f12;
  }
  ul {
    padding: 0;
    li {
      list-style: none;
      &:before {
        content: '•';
        display: inline-block;
        color: #d54f12;
        margin-right: 25px;
      }
    }
    ol {
      padding: 0;
      li {
        &:first-child {
          &:before {
            content: '1.';
            color: #666565;
            margin-right: 21px;
          }
        }
        &:last-child {
          &:before {
            content: '2.';
            color: #666565;
            margin-right: 21px;
          }
        }
      }
    }
    &.big-size {
      font-size: 20px;
    }
  }
  .desc-service {
    display: flex;
  }
  .information-message {
    color: #d54f12;
    font-size: 20px;
  }
  .order-service {
    color: #fff;
    background-color: #d75920;
    margin: 40px auto 0;
    width: 200px;
    padding: 11px 0;
    text-align: center;
    @include transition();
    &:hover {
      cursor: pointer;
      transform: scale(.95);
    }
  }

  .repair {
    .information-title {
      display: flex;
      align-items: center;
      img {
        margin-right: 60px;
        margin-left: 25px;
      }
    }
    .information-second-title {
      margin-top: 0;
    }
  }

  .obsl {
    .information-title {
      display: flex;
      align-items: center;
      img {
        margin: 0 45px 0 20px;
      }
      .wrapper-title {
        .desc-title {
          font-size: 16px;
          text-transform: initial;
          span {
            color: #d54f12;
          }
        }
      }
    }
    .information-second-title {
      margin-top: 0;
    }
    .desc-service {
      &.phone {
        p {
          width: 50%;
        }
      }
    }
  }

  @media all and (max-width: 1023px) {
    width: 100%;
    left: 0;
    margin-left: 0;
    top: 10%;
    padding: 40px 10px;
    font-size: 14px;
    .information-second-title {
      font-size: 14px;
      margin-top: 0;
    }
    .information-message {
      font-size: 18px;
      br {
        display: none;
      }
    }
    .desc-service {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .information-title {
      text-align: center;

    }
    ul {
      &.big-size {
        font-size: 14px;
      }
    }
    .repair {
      .information-title {
        flex-direction: column;
        text-align: center;
        img {
          margin: 0;
          width: 100%;
          max-width: 400px;
        }
      }
    }

    .obsl {
      .information-title {
        flex-direction: column;
        .wrapper-title {
          .desc-title {
            text-align: left;
            font-size: 14px;
            margin-top: 1em;
          }
        }
      }
      .information-second-title{
        font-size: 16px;
        text-align: center;
      }
      .desc-service.phone{
        font-size: 16px;
        text-align: center;
        padding: 0 20px;

        p{
          width: 100%;
          &:first-child{
            br{
              display: none;
            }
          }
        }

      }
    }
  }
}

#thank {
  position: fixed;
  left: 50%;
  margin-top: 170px;
  margin-left: -157.5px;
  z-index: 3;
  width: 315px;
  color: #fff;
  text-align: center;
  height: 123px;
  border: 1px solid #fff;
  background-color: #d75920;
  .block-messages {
    font-size: 14px;
    font-style: italic;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
}

.modal-block {
  visibility: hidden;
  position: relative;
  margin: 5% auto;
  font-family: 'Roboto', sans-serif;
  background-color: #fff;
  box-sizing: border-box;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  top: -500px;
}

.close__modal {
  position: absolute;
  right: 5px;
  top: 5px;
  z-index: 4;
  font-size: 19px;
  @include icon(close);
  @include transition();
  &:hover {
    cursor: pointer;
  }
}

.close-modal:hover {
  cursor: pointer;
  color: #455458;
}

