@import "variable";
@import "icons";

.title {
  padding: 12px 0;
  font-size: 30px;
  background-color: #5d5d5d;
  color: white;
  text-align: center;
  margin-bottom: 12px;
}

section {
  margin-top: 25px;
}

.section-slider {
  margin-top: 0;
  .top-slider {
    margin-top: 2px;
    width: 1140px;
    height: 490px;
    .slide-item {
      width: 1140px;
      height: 490px;
      float: left;
      background-attachment: fixed;
      background-size: auto;
      background-position: center top;
      background-color: transparent;
      background-repeat: no-repeat;
      .desc-slider {
        position: relative;
        top: 60px;
        left: 90px;
        line-height: 55px;
        color: #332d25;
        font-weight: 600;
        .first-line {
          text-transform: uppercase;
          font-size: 42px;
        }
        .second-line {
          font-size: 53px;
          text-transform: uppercase;
        }
        .third-line {
          font-size: 28px;
        }
      }
    }
  }
  .nav-topslider {
    position: absolute;
    width: 100%;
    top: 48%;
    .prev, .next {
      position: absolute;
      font-size: 30px;
      color: black;
    }
    .prev {
      left: 10px;
      @include icon(prev);
    }
    .next {
      right: 10px;
      @include icon(next);
    }
  }
}

.about {
  .about-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .list-item {
      width: 273px;
      height: 222px;
      margin-bottom: 17px;
      position: relative;
      padding: 17px 10px 0 30px;
      color: #302a23;
      font-size: 14px;
      @include transition();
      @for $i from 1 through 8 {
        &:nth-child(#{$i}) {
          background: url('#{$inludePath}img/about#{$i}.jpg') no-repeat;
          @include icon(about#{$i});
        }
      }
      &:before, .item-title, .item-desc {
        position: relative;
        z-index: 2;
      }
      .item-title {
        font-size: 20px;
        margin: 15px 0 5px;
        min-height: 38px;
        line-height: 18px;
        font-weight: 600;
        display: flex;
        align-items: center;
      }
      &:before {
        font-size: 65px;

      }
      &:nth-child(2) {
        padding-top: 26px;
        &:before {
          font-size: 56px;
        }
      }
      &:nth-child(5), &:nth-child(6), &:nth-child(7), &:last-child {
        margin-bottom: 0;
      }
      &:after {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(233, 227, 219, 0.82);
        @include transition();
      }
      &:hover {
        cursor: pointer;
        color: white;
        &:after {
          background-color: rgba(215, 89, 32, 0.76);
        }
      }
    }
  }
}

.stock {
  &:hover {
    cursor: pointer;
  }
}

.container-three-block {
  background: url('#{$inludePath}/img/bg-block-three.jpg') center 110% no-repeat;
}

.service {
  margin-top: 50px;
  .service-list {
    display: flex;
    justify-content: center;
    margin-top: 65px;
    .list-item {
      position: relative;
      width: 341px;
      height: 272px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 15px;
      font-size: 30px;
      text-decoration: underline;
      text-transform: uppercase;
      color: transparent;
      @include transition();
      span {
        position: relative;
        z-index: 4;
      }
      &:after {
        content: '';
        position: absolute;
        z-index: 3;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: transparent;
        @include transition();
      }
      &:hover {
        cursor: pointer;
        color: white;
        &:after {
          background-color: rgba(215, 89, 32, 0.76);
        }

      }
      @for $i from 1 through 3 {
        &.service#{$i} {
          background: url('#{$inludePath}img/service#{$i}.jpg') center no-repeat;
        }
      }
    }

  }

}

.work-with-you {
  margin-top: 55px;
  font-family: 'Roboto', sans-serif;
  .title {
    margin-bottom: 0;
  }
  .step-work {
    background-color: #e9e3db;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    .step-item {
      position: relative;
      display: flex;
      align-items: center;
      margin: 0 30px;
      top: 25px;
      &:before {
        font-size: 50px;
        position: relative;
        top: -7px;
      }
      &:after {
        content: '';
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: -50px;
        left: -55px;
        color: white;
        font-size: 30px;
        background-color: #ea6427;
        width: 40px;
        height: 40px;
        border-radius: 25px;
      }
      &:first-child {
        @include icon(phone1);
        &:before {
          margin-right: 15px;
          top: -14px;
        }
        &:after {
          content: '1';
        }
      }
      &:nth-child(2) {
        @include icon(pencil);
        &:before {
          margin-right: 30px;
          top: -14px;
        }
        &:after {
          content: '2';
        }
      }
      &:nth-child(3) {
        @include icon(delivery);
        &:before {
          margin-right: 40px;
        }
        &:after {
          content: '3';
        }
      }
      &:nth-child(4) {
        @include icon(conditioner);
        &:before {
          margin-right: 20px;
        }
        &:after {
          content: '4';
        }
      }
    }
  }
}

.clients {

  .clients-list {
    padding: 60px 0;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 20%;
    }
  }
}
@media all and (max-width: 1139px){
  .about{
    .about-list{
      .list-item{
        width: 25%;
      }
    }
  }
}

@media all and (max-width: 991px) {

  .title {
    font-size: 30px;
    text-align: center;
  }

  .section-slider {
    .top-slider {
      .slide-item {
        height: 500px;
        background-size: inherit;
        background-attachment: inherit;
        .desc-slider {
          text-align: left;
          line-height: 35px;
          position: relative;
          top: 75px;
          left: 0;
          padding-left: 33px;
          .first-line {
            font-size: 40px;
          }
          .second-line {
            font-size: 40px;
          }
          .third-line {
            font-size: 30px;
          }
        }
        &#slide3 {
          .desc-slider{
            padding-left: 55px;
          }
        }
      }
    }

  }

  .about {
    .about-list {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .list-item {
        width: 100%;
        max-width: 275px;
        height: initial;
        padding: 30px;
        text-align: center;
        &:before {
          color: #df4c06;
        }
        &:hover {
          &:before {
            color: #fff;
          }
        }
        .item-title {
          justify-content: center;
          margin: 5px 0;
        }
        .item-desc {
          display: none;
          br {
            display: none;
          }
        }
        &:nth-child(5), &:nth-child(6), &:nth-child(7), &:last-child {
          margin-bottom: 17px;
        }
      }
    }
  }

  .stock {
    .stock-items-mobile {
      img {
        margin: 0 auto;
      }
    }
  }

  .work-with-you {

    .step-work {
      padding: 70px 0 30px;
      height: initial;
      flex-direction: column;
      .step-item {
        margin: 0 auto 70px;
        left: 30px;
        width: 230px;
        top: initial;
        &:last-child {
          margin-bottom: 0;
        }
        &:after {
          top: -45px;
          left: -50px;
        }
        &:first-child {
          &:before {
            margin-right: 15px;
          }
        }
        &:nth-child(2) {
          &:before {
            margin-right: 15px;
          }
        }
        &:nth-child(3) {
          &:before {
            margin-right: 15px;
          }
        }
        &:nth-child(4) {
          &:before {
            margin-right: 15px;
          }
        }
      }
    }
  }

  .clients {

    .clients-list {
      max-width: 310px;
      margin: 0 auto;
      width: 100%;
    }
  }
}

@media all and (max-width: 750px) {
  .section-slider {
    .top-slider {
      .slide-item {
        background-position: left;
        &#slide1 {
          background-position: -225px;
        }
        &#slide2 {
          background-position: -225px;
        }
        &#slide3 {
          background-position: -120px;
        }
        &#slide4 {
          background-position: 56%;
        }
        .desc-slider {
          width: 270px;
          .first-line {
            font-size: 30px;
          }
          .second-line {
            font-size: 30px;
          }
          .third-line {
            font-size: 20px;
          }
        }
      }
    }
  }
}

@media all and (max-width: 450px) {
  .section-slider {
    .top-slider {
      .slide-item {
        &#slide2 {
          background-position: -365px;
        }
      }
    }
  }
}
